import { SambaSourceConfig } from "./types";
import { BaseConnectorConfig } from '@xip-online-data/types';


export function generateCollectionName(connectorConfig: BaseConnectorConfig, sourceConfig: SambaSourceConfig): string {
  return `${connectorConfig.datasourceIdentifier}_${sourceConfig.sambaIdentifier}`;
}

export function generateKafkaTopic(connectorConfig: BaseConnectorConfig, sourceConfig: SambaSourceConfig): string {
  return `${connectorConfig.tenantIdentifier}_SOURCE_${connectorConfig.datasourceIdentifier}_${sourceConfig.sambaIdentifier}`;
}
